* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.product {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 0;
}

.product__photo {
  background-size: cover;
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.product__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__subtitle {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.product__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 6px;
  display: flex;
}

.product__review-count {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__price {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.product__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__buy-button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product__buy-button:hover {
  cursor: pointer;
  color: #00acdc;
  background-color: #fff;
  transition: all .3s;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--count-4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
